<docs>
# PageThematiqueSingle
> The CG thematique page single

@author Kffein <contact@kffein.com>
</docs>

<script lang="ts">
import { templateRef, tryOnUnmounted, until, VueInstance } from '@vueuse/core';
import SvgArrow from 'assets/svg/arrow.svg?raw';
import CloseIcon from 'assets/svg/close.svg?raw';
import IconPlus from 'assets/svg/plus.svg?raw';
import StarIcon from 'assets/svg/star.svg?raw';
import axios from 'axios';
import { store as scrollStore } from 'components/SmoothScroll/store';
import { store as loaderStore } from 'components/ui/Loader/store';
import AudioPlayer from 'components/UiKit/AudioPlayer/AudioPlayerBar.vue';
import { WebGLSize } from 'components/WebGL/gl';
import { store as themeStore } from 'pages/Thematique/store';
import emitter, { EVENTS } from 'plugins/store/events';
import { interactWithSceneAt } from 'plugins/webgl/three';
import { store as appStore } from 'store/app';
import usePageFlow from 'utils/flow/page';
import { gsap } from "vendors/gsap";
import { computed, defineComponent, onBeforeMount, provide, ref, toRefs } from 'vue';
import { useRoute, useRouter } from 'vue-router';
import BorderSvg from "./assets/border.svg?raw";
import ArticleObject from "./blocks/ArticleObject.vue";
import InteractiveObject from "./blocks/InteractiveObject.vue";

export interface VueAnimate extends VueInstance {
  animateIn(): void;
  animateOut(): void;
}

export default defineComponent({
  name: "PageThematiqueSingle",
  components: {
    AudioPlayer,
    ArticleObject,
    InteractiveObject
  },
  setup() {
    const route = useRoute();
    const router = useRouter();
    const Panel = templateRef<HTMLElement>("Panel");
    const Content = templateRef<HTMLElement>("Content");
    const LeftSide = templateRef<HTMLElement>("LeftSide");
    const el = templateRef<HTMLElement>("el");
    const disabled = computed(() => appStore.disabled);
    const showArticle = ref(false); //to show article on click
    const zoomedArticle = ref(false);
    const { done } = toRefs(loaderStore.state);
    const isDetailReady = ref(false);
    const reducerSize = ref({ width: 0, height: 0 });

    //check if the thematique page is in the store (exists)
    const isValidThematique = themeStore.findTheme(`/${route.params.thematique as string}/`);

    const { readyToRender, getData, onInitUi } = usePageFlow<ObjectSingle>({
      load() {

        if (!isValidThematique) {
          router.push('/');
          return Promise.resolve();
        }

        return axios.get(`/thematiques/${route.params.thematique}/data.json`)
          .then(({ data }) => {
            themeStore.update(data);
            themeStore.setCurrent(data.id);
            const object = themeStore.findObjectBySlug(route.params.slug as string);

            if (object) {
              // console.log("Found object:", object);
            } else {
              appStore.setIs404();
            }
            return {
              parent: data,
              ...object
            }
          })
      }
    });

    onBeforeMount( () => {
      if(!appStore.introSeen && appStore.redirection) {
        router.push("/");
        return;
      }
    });

    onInitUi(async () => {
      if (!disabled.value) appStore.disableApp(true);

      // set the background color
      if (themeStore.current) {
        appStore.setAppBgColor(themeStore.current.bgColor);
        themeStore.setNextVariant();
      }

      scrollStore.setScrollable(false);
      emitter.on(EVENTS.OBJECT_DETAIL_READY, onDetailReady);
      emitter.on(EVENTS.OBJECT_SHOW_ARTICLE, onShowArticle);

      await until(done).toBe(true);

      let delay = 0;

      if (page.value.type !== 'video' && page.value.type !== 'article') {
        await until(isDetailReady).toBe(true);

        delay = 200;
      }

      // animation
      setTimeout(() => {
        animateIn();
      }, delay);
    });

    // Animation Transition In
    const animateIn = () => {
      gsap.timeline()
        .add(() => {
          Panel.value.classList.remove("hidden");
          Content.value.classList.remove("hidden");
          interactWithSceneAt(0, {
            callback: "show",
          })
        }, 0)
        .fromTo(Panel.value, { xPercent: 105, }, { xPercent: 0, opacity: 1, ease: "power4.out", duration: 1.5 }, 0.4)
        .fromTo(LeftSide.value, { xPercent: -105 }, { xPercent: 0, opacity: 1, ease: "power4.out", duration: 1.5 }, 0.2)
        .add(() => {
          interactWithSceneAt(0, {
            callback: "toggleShowingInside",
            params: true
          })
        }, .8)
        .add(() => {

          el.value.querySelector(".close")?.classList.add("fixed");

          appStore.disableApp(false);
        }, 1.5);
    }

    // Leaving the page - Clearing 
    tryOnUnmounted(() => {
      scrollStore.setScrollable(true);
    });

    const goBackListing = () => {
      themeStore.toggleFromListing(true);
      appStore.disableApp(true);
      gsap.timeline()
        .to(el.value, { opacity: 0, ease: "power4.out", duration: 1 }, 0)
        .add(() => {
          router.push({ name: 'thematique', params: { slug: route.params.thematique } });
        }, 1);
    }

    const onDetailReady = () => {
      isDetailReady.value = true;
    }

    const onShowArticle = (size: any | WebGLSize) => {
      reducerSize.value = { width: size?.width * 1.5, height: size?.height * 1.5 };
      showArticle.value = true;
    }

    const onBackToInteractive = () => {
      showArticle.value = false;

      setTimeout(() => {
        interactWithSceneAt(0, {
          callback: "revertToInteractive"
        });
      }, 100)
    }

    const onZoomedArticle = (value: boolean) => {
      zoomedArticle.value = value;
    }

    const onClickCredit = (e: any, route: any) => {
      e.preventDefault();
      appStore.disableApp(true);

      gsap.timeline()
        .to(el.value, { opacity: 0, ease: "power4.out", duration: 1 }, 0)
        .add(() => {
          router.push(route);
        }, 1);
    }

    tryOnUnmounted(() => {
      emitter.off(EVENTS.OBJECT_DETAIL_READY, onDetailReady);
      emitter.off(EVENTS.OBJECT_SHOW_ARTICLE, onShowArticle);
    })

    const page = getData();
    provide("page", page);

    return {
      readyToRender,
      done,
      page,
      disabled,
      goBackListing,
      onBackToInteractive,
      onZoomedArticle,
      onClickCredit,
      Panel,
      StarIcon,
      CloseIcon,
      el,
      showArticle,
      zoomedArticle,
      SvgArrow,
      BorderSvg,
      IconPlus,
      route,
      LeftSide,
      reducerWidth: computed(() => `${reducerSize.value.width}px`),
      reducerHeight: computed(() => `${reducerSize.value.height}px`),
    };
  },
});
</script>

<template>
  <main v-if="readyToRender && done && page"
        ref="el"
        :key="page.parent?.id"
        :data-variant="page.parent?.id"
        :data-disabled="disabled"
        class="page thematique-single"
  >
    <div ref="Content"
         class="content hidden"
         :data-variant="page.parent?.id"
    >
      <div ref="LeftSide" class="left-section">
        <ArticleObject v-if="(page.type === 'article' && page.article) || (showArticle && page.article)"
                       :article="page.article"
                       :type="page.type"
                       :theme-id="page.parent?.id"
                       :variant="page.parent?.id"
                       :no-zoom="page.noZoom"
                       @zoomed="onZoomedArticle"
        />
        <InteractiveObject v-if="page.type === 'book' || page.type === 'cd' || page.type === 'vinyl'"
                           :id="page.id"
                           :type="page.type"
                           :article="page.article"
                           :article_scale="page.article_scale"
                           :article_offset="page.article_offset"
                           :pochette_recto="page.pochette_recto"
                           :pochette_verso="page.pochette_verso"
                           :tranche="page.tranche"
                           :variant="page.parent?.id"
                           :page_1="page.page_1"
                           :page_2="page.page_2"
        />
        <ui-video v-if="page.type === 'video' && page.video"
                  class="media -video"
                  :poster="`/thematiques/${route.params.thematique}/contenu/thumbnails/${page.poster}`"
                  :src="`/thematiques/${route.params.thematique}/contenu/${page.video}`"
        />
        <div :class="['wrapper-reducer', { hidden: !showArticle || zoomedArticle }]" @click="onBackToInteractive" />
      </div>
      <div ref="Panel"
           class="contenu-side-panel hidden"
           data-lenis-prevent
           :data-variant="page.parent?.id"
      >
        <div class="inner-panel">
          <div class="inner-top">
            <ui-button class="close"
                       theme="icon"
                       :variant="page.parent?.id"
                       :icon="CloseIcon"
                       @click="goBackListing()"
            />
            <div v-if="page.note" class="note">
              <span v-for="(note, index) in 5"
                    :key="index"
                    :class="{
                      fill: (index + 1) <= Math.floor(page.note),
                      half: (index + 1) === Math.ceil(page.note) && page.note % 1 !== 0,
                    }"
                    class="icon-star"
              >
                <span class="top" v-html="StarIcon" />
                <span class="bottom" v-html="StarIcon" />
              </span>
              <span class="note-text">NOTE DE CLAUDE GINGRAS</span>
            </div>
            <h1 class="title-single" v-html="page.title" />
            <div class="text wysiwyg" v-html="page.description" />
            <div class="credits">
              <ui-button theme="link"
                         label="CrÃ©dits"
                         :to="{ name: 'credit', query: { section: page.id } }"
                         :variant="page.parent?.id"
                         tag="a"
                         @click="onClickCredit($event, { name: 'credit', query: { section: page.id } })"
              />
            </div>
          </div>
          <div v-if="page.sound"
               class="bottom-sticky"
               :data-variant="page.parent?.id"
          >
            <AudioPlayer :audio-src="`/thematiques/${route.params.thematique}/contenu/${page.sound}`" />
          </div>
        </div>
      </div>
    </div>
  </main>
</template>

