<docs>
  # InteractiveObject
  > A component that store a webgl scene for an interactive object
  
  @author Kffein <contact@kffein.com>
</docs>

<script lang="ts">
import { templateRef, tryOnUnmounted } from "@vueuse/core";
import emitter, { EVENTS } from "plugins/store/events";
import { clearWebGL, useWebGL } from "plugins/webgl/three";
import useFlow from "utils/flow";
import { defineComponent, ref, toRefs } from 'vue';
import { useI18n } from 'vue-i18n';
import { useRoute } from "vue-router";

export default defineComponent({
  name: "InteractiveObject",
  props: {
    id: {
      type: String,
      default: null
    },
    variant: {
      type: String,
      default: null
    },
    type: {
      type: String,
      default: "vinyl"
    },
    article: {
      type: [String, Array],
      default: null
    },
    article_scale: {
      type: Number,
      default: 2
    },
    article_offset: {
      type: Number,
      default: 0
    },
    pochette_recto: {
      type: String,
      default: null
    },
    pochette_verso: {
      type: String,
      default: null
    },
    tranche:{
      type: String,
      default: null
    },
    page_1: {
      type: String,
      default: null
    },
    page_2: {
      type: String,
      default: null
    }
  },
  setup(props) {
    const { t } = useI18n();
    const Scene = templateRef<HTMLElement>("Scene");
    const route = useRoute();
    const showSpinner = ref(true);

    const { onInitUi } = useFlow({
      ui: { 
        vinyl: () => import("./webgl/VinylScene"),
        book: () => import("./webgl/BookScene"),
        cd: () => import("./webgl/CDScene"),
       },
      }
    );

    const folder = `/thematiques/${route.params.thematique}/contenu/`
    
    onInitUi( ({ui}) => {
      emitter.on(EVENTS.OBJECT_DETAIL_READY, onReady);
      
      let scene = ui.vinyl.VinylScene;
      if(props.type === "book") scene = ui.book.BookScene;
      if(props.type === "cd") scene = ui.cd.CDScene;

      useWebGL(
      [
        { 
          glScene: scene,
          settings: {
            folder,
            id: props.id,
            tranche: props.tranche,
            recto: props.pochette_recto,
            verso: props.pochette_verso,
            article: props.article,
            article_scale: props.article_scale ?? 2,
            article_offset: props.article_offset ?? 0,
            page_1: props.page_1,
            page_2: props.page_2,
          }
        },
      ], { antialias:true, canvasContainer: Scene.value, alpha:true, sizeBounds: {el:Scene.value} });

      
    });
    
    const onReady = () => {
      showSpinner.value = false;
    }
    
    tryOnUnmounted(() => {
      emitter.off(EVENTS.OBJECT_DETAIL_READY, onReady);
      clearWebGL(true);
    })

    return {
      t,
      ...toRefs(props),
      showSpinner
    };
  },
});
</script>

<template>
  <section ref="el"
           class="InteractiveObject"
  >
    <div ref="Scene" class="sceneInteractive" />
    <span :data-show="showSpinner" class="spinner" />
  </section>
</template>

<style lang="stylus" scoped>
  .InteractiveObject
    position absolute
    size 100%
    f(center)

  .sceneInteractive
    position relative
    size 100% 100%

  .spinner
    absolute 0
    top 50%
    left calc("50vw - 10px")
    size 26px
    border 1px solid
    addVariantProps(border-top-color, $dark:false)
    border-bottom-color transparent
    border-radius 50%
    transform-origin center
    display inline-block
    box-sizing border-box
    animation rotation 1s linear infinite
    pointer-events none
    opacity 0
    trans(opacity, 1s)
    &[data-show="true"]
      opacity 1

@keyframes rotation 
  0%
    transform: translate(-50%, -50%) rotate(0deg)

  100%
    transform: translate(-50%, -50%) rotate(360deg)
  </style>
