<script lang="ts">
import { VueInstance, templateRef } from '@vueuse/core';
import gsap from "gsap";
import emitter, { EVENTS } from 'plugins/store/events';
import { defineComponent, inject, onBeforeUnmount, onMounted, ref } from 'vue';
import PauseIcon from './assets/pause.svg?raw';
import PlayIcon from './assets/play.svg?raw';
import PlayerIcon from './assets/player-visualizer.svg?raw';

export default defineComponent({
  name: "AudioPlayer",
  props: {
    audioSrc: {
      type: String,
      required: true,
    },
  },
  setup(props) {
    const el = templateRef<VueInstance>("el");
    const audio = ref<HTMLAudioElement | null>(null);
    const isPlaying = ref(false);
    const currentTime = ref(0);
    const duration = ref(0);
    const visualizer = ref<HTMLDivElement | null>(null);

    let animation: gsap.core.Tween | null = null; // GSAP timeline for animations

    const togglePlay = () => {
      if (!audio.value) return;

      if (isPlaying.value) {
        audio.value.pause();
        stopVisualizer();
        emitter.emit(EVENTS.RESET_INACTIVE);
      } else {
        audio.value.play();
        startVisualizer();
        emitter.emit(EVENTS.STOP_INACTIVE);
      }


      isPlaying.value = !isPlaying.value;
    };

    const startVisualizer = () => {
      if (!audio.value) return;
      const paths = el.value?.$el.querySelectorAll(".visualizer path");
      const progress = audio.value.currentTime / audio.value.duration; // Calculate progress
      const activePaths = Math.floor(progress * paths.length); // Calculate how many paths to animate

      // Animate opacity with GSAP
      paths.forEach((pathSingle: any, index: any) => {
        const pathElement = paths[index];
        if (index < activePaths) {
          gsap.to(pathElement, { opacity: 1, duration: 0.2, overwrite: "auto" });
        } else {
          gsap.to(pathElement, { opacity: 0.6, duration: 0.2, overwrite: "auto" });
        }
      });
    };
    const stopVisualizer = () => {
      if (animation) (animation as gsap.core.Tween).kill(); // Stop GSAP animation
    };

    const formatTime = (time: number) => {
      const minutes = Math.floor(time / 60);
      const seconds = Math.floor(time % 60)
        .toString()
        .padStart(2, "0");
      return `${minutes}:${seconds}`;
    };

    const seekAudio = (event: MouseEvent) => {
      if (!audio.value || !visualizer.value) return;
      event.stopPropagation();
      const rect = visualizer.value.getBoundingClientRect();
      const clickX = event.clientX - rect.left;
      const newTime = (clickX / rect.width) * duration.value;

      audio.value.currentTime = newTime;
      currentTime.value = newTime; // Update UI immediately
    };

    onMounted(() => {
      if (!audio.value) return;

      audio.value.addEventListener("timeupdate", onTimeUpdate);
      audio.value.addEventListener("loadedmetadata", onMetadata);
      audio.value.addEventListener("ended", onEnded);
    });

    const onTimeUpdate = () => {
      if (!audio.value) return;
      currentTime.value = audio.value.currentTime;
      startVisualizer(); // Call the visualizer on every timeupdate
    }

    const onMetadata = () => {
      if (!audio.value) return;
      duration.value = audio.value?.duration;
    }

    const onEnded = () => {
      togglePlay();
    }

    onBeforeUnmount(() => {
      stopVisualizer(); // Stop animation on unmount
      emitter.emit(EVENTS.RESET_INACTIVE);
    });

    const page = inject('page') as ObjectSingle;

    return {
      audio,
      el,
      page,
      ...props,
      isPlaying,
      visualizer,
      currentTime,
      togglePlay,
      formatTime,
      seekAudio,
      PlayIcon,
      PauseIcon,
      PlayerIcon,
    };
  },
});
</script>

<template>
  <ui-button ref="el"
             class="media-player"
             theme="reset"
             :data-variant="page.parent?.id"
             @click="togglePlay"
  >
    <audio ref="audio" :src="audioSrc" />

    <div class="progress-container">
      <div class="play-button" theme="reset">
        <span class="icon-play-pause" v-html="isPlaying ? PauseIcon : PlayIcon" />
      </div>
      <span v-if="!isPlaying" class="text-not-play">Ã‰couter l'extrait</span>
      <div ref="visualizer"
           class="visualizer"
           :class="isPlaying ? 'active' : 'hidden'"
           @click="seekAudio"
           v-html="PlayerIcon"
      />

      <span v-if="isPlaying" class="time">{{ formatTime(currentTime) }}</span>
    </div>
  </ui-button>
</template>
