<docs>
  # ArticleObject
  > A component that store an image slider to navigate through an article
  
  @author Kffein <contact@kffein.com>
</docs>

<script lang="ts">
import { templateRef, tryOnUnmounted, until, VueInstance } from "@vueuse/core";
import SvgArrow from 'assets/svg/arrow.svg?raw';
import { default as CloseIcon } from 'assets/svg/close.svg?raw';
import IconPlus from 'assets/svg/plus.svg?raw';
import { store as loaderStore } from "components/ui/Loader/store";
import { each, map } from "lodash";
import { nextTick } from "process";
import { store as interfaceStore } from "store/interface";
import useFlow from "utils/flow";
import { Draggable, gsap } from "vendors/gsap";
import { computed, defineComponent, onBeforeUnmount, PropType, reactive, ref, toRefs } from 'vue';
import { useI18n } from 'vue-i18n';
import { useRoute } from "vue-router";
import BorderSvg from "../assets/border.svg?raw";

export interface Data {
  optionsSlider: Record<string, unknown>,
}

export default defineComponent({
  name: "ArticleObject",
  props: {
    article: {
      type: [String, Array],
      default: null
    },
    type: {
      type: String,
      default: "article"
    },
    themeId: {
      type: String,
      default: null
    },
    noZoom: {
      type: [Boolean, Array] as PropType<boolean | boolean[]>,
      default: false
    }
  },
  emits: ["zoomed"],
  setup(props, { emit }) {
    const { t } = useI18n();
    const route = useRoute();
    const elPrev = ref<O<VueInstance>>()
    const elNext = ref<O<VueInstance>>()
    const el = templateRef<HTMLElement>("el");
    let initialOffsetX = 0;
    let initialOffsetY = 0;
    const zoomedImageSrc = ref<string | null>(null);
    const isZoomed = ref(false);
    const zoomedImageElement = ref<HTMLElement | null>(null);
    const lastTouchPosition = reactive({ x: 0, y: 0 });
    const { done } = toRefs(loaderStore.state)
    const imageSize = ref<number[]>([]);

    const data: Data = reactive({
      optionsSlider: {
        loop: true,
        pagination: false,
        centeredSlides: true,
        overflow: false,
        // mobile
        spaceBetween: 20,
        slidesPerView: 1,
        breakpoints: {
          // desktop
          1001: {
            spaceBetween: 45,
            slidesPerView: 1,
          },
        },
        on: {
          slideChangeTransitionStart: () => {
            gsap.timeline()
              .set(".button-zoom", { opacity: 0, scale: 0, overwrite: true }, 0)
          },
          slideChangeTransitionEnd: () => {
            updateZoomButton();
          },
        }
      },
    });

    const { onInitUi } = useFlow();

    onInitUi(async () => {
      gsap.timeline()
          .set(".button-zoom", { opacity: 0, scale: 0, overwrite: true }, 0)

      await until(done).toBe(true);

      setTimeout(() => {
        updateZoomButton();
      }, 1000)

    });

    const updateZoomButton = () => {

      gsap.fromTo(".button-zoom", { opacity: 0, scale: 0 }, { opacity: 1, scale: 1, ease: "power4.out", duration: .5, delay: .1 })

      // get all images size
      const slides = el.value?.querySelectorAll('.slide') as NodeListOf<HTMLElement>;

      each(slides, pic => {
        const bounds = pic.querySelector('.picture')!.getBoundingClientRect();
        const imgBounds = pic.querySelector('.img')!.getBoundingClientRect();
        const index = parseInt(pic?.getAttribute("data-swiper-slide-index") || '0');
        imageSize.value[index] = imgBounds ? (bounds.width - imgBounds.width) / 2 : 0
      });
    }

    const isSwiper = computed(() => {
      //check if page.article is an array and has more than 1 items
      if (Array.isArray(props.article)) {
        return props.article?.length > 1;
      }
      return false;
    });

    const normalizedArticle = computed(() => {
      const extension = interfaceStore.hasWebP ? ".webp" : ".jpg";
      if (Array.isArray(props.article)) {

        return map(props.article, (article: string) => {
          const jpg = article.indexOf(".jpg");
          const png = article.indexOf(".png");

          return article.substring(0, (jpg !== -1) ? jpg : png) + extension;
        });
      }
      if (typeof props.article === "string") {
        const jpg = props.article.indexOf(".jpg");
        const png = props.article.indexOf(".png");
        return [(props.article as string).substring(0, (jpg !== -1) ? jpg : png) + extension];
      }
      return []; // Fallback if it's neither a string nor an array
    });

    // Normalize noZoom to an array of booleans
    const normalizedNoZoom = computed(() => {
      return Array.isArray(props.noZoom) ? props.noZoom : [props.noZoom];
    });

    // You can now safely use normalizedNoZoom as an array of booleans
    const isNoZoom = (index: number) => normalizedNoZoom.value[index] ?? false;
    const startZoom = (imageSrc: string, event: MouseEvent | TouchEvent) => {
      zoomedImageSrc.value = imageSrc;
      isZoomed.value = true;

      nextTick(() => {
        zoomedImageElement.value = document.querySelector(".zoomed-image") as HTMLElement;
        if (!zoomedImageElement.value || !el.value) return;

        const scale = 2; // Adjust zoom level as needed
        const sectionBounds = el.value.getBoundingClientRect();
        //const imageBounds = zoomedImageElement.value.getBoundingClientRect();

        let clientX = 0;
        let clientY = 0;

        if (event instanceof MouseEvent) {
          clientX = event.clientX;
          clientY = event.clientY;
        } else if (event instanceof TouchEvent && event.touches.length === 1) {
          event.preventDefault(); // Stop scrolling
          const touch = event.touches[0];
          clientX = touch.clientX;
          clientY = touch.clientY;
        }

        // Calculate position relative to image
        const offsetX = clientX - sectionBounds.left;
        const offsetY = clientY - sectionBounds.top;

        // Convert to percentage for transform-origin
        const percentX = (offsetX / sectionBounds.width) * 100;
        const percentY = (offsetY / sectionBounds.height) * 100;

        const tl = gsap.timeline()
          .set(zoomedImageElement.value, {
            scale,
            transformOrigin: `${percentX}% ${percentY}%`, // Center zoom at tap/click
            x: 0,
            y: 0,
            opacity: 0, // Start hidden to prevent flash
          }, 0);

        if (!interfaceStore.hasInteract.article) {
          tl.from(".button-infos-touch", { opacity: 0, scale: 0, ease: "power4.out", duration: 1 }, .4)
        }

        // Store initial offsets for movement
        initialOffsetX = offsetX;
        initialOffsetY = offsetY;

        // Add event listeners
        if (!interfaceStore.hasTouch) {
          el.value.addEventListener("mousemove", onMove);
        }

        emit("zoomed", true);

        // Remove any existing Draggable instance before creating a new one
        Draggable.get(zoomedImageElement.value)?.kill();

        // Using GSAP Draggable for touch events
        if (interfaceStore.hasTouch) {

          Draggable.create(zoomedImageElement.value, {
            type: "x,y",
            bounds: el.value,
            onDragStart: () => {
              if(!interfaceStore.hasInteract.article) {
                interfaceStore.toggleInteraction(true, 'article');
              }
            }
          });
        }

        // Fade the image in when zoom starts
        gsap.to(zoomedImageElement.value, {
          opacity: 1,
          duration: 0.3,
          delay: 0.2, // Adjust delay if needed
          ease: "power2.out",
        });
      });
    };


    const stopZoom = () => {
      isZoomed.value = false;
      zoomedImageSrc.value = null;

      el.value.removeEventListener("mousemove", onMove);
      // Remove Draggable event listeners
      Draggable.get(zoomedImageElement.value)?.kill();
      emit("zoomed", false);
    };

    const onMove = (event: MouseEvent | TouchEvent) => {
      if (!isZoomed.value || !zoomedImageElement.value || !el.value) return;

      let clientX = 0;
      let clientY = 0;

      if (event instanceof MouseEvent) {
        clientX = event.clientX;
        clientY = event.clientY;
      } else if (event instanceof TouchEvent && event.touches.length === 1) {
        event.preventDefault();
        const touch = event.touches[0];
        clientX = touch.clientX;
        clientY = touch.clientY;
      }

      const sectionBounds = el.value.getBoundingClientRect();
      const imageBounds = zoomedImageElement.value.getBoundingClientRect();

      // Calculate max translation limits
      const maxOffsetX = imageBounds.width - sectionBounds.width;
      const maxOffsetY = imageBounds.height - sectionBounds.height;

      // Get cursor position as a percentage of the section
      const percentX = (clientX - sectionBounds.left) / sectionBounds.width;
      const percentY = (clientY - sectionBounds.top) / sectionBounds.height;

      // Move the image relative to the initial zoom position
      const translateX = initialOffsetX + percentX * maxOffsetX * -1;
      const translateY = initialOffsetY + percentY * maxOffsetY * -1;

      gsap.to(zoomedImageElement.value, {
        x: translateX,
        y: translateY,
        duration: 0.1,
        ease: "power2.out",
      });
    };


    const onTouchStart = (event: TouchEvent) => {
      if (!isZoomed.value || !zoomedImageElement.value || event.touches.length !== 1) return;

      const touch = event.touches[0];
      lastTouchPosition.x = touch.clientX;
      lastTouchPosition.y = touch.clientY;
    };

    onBeforeUnmount(() => {
      if (isZoomed.value) stopZoom();
    });

    tryOnUnmounted(() => {
      gsap.globalTimeline.clear(); // Clear any ongoing animations
    })

    return {
      ...toRefs(props),
      t,
      route,
      startZoom,
      stopZoom,
      isZoomed,
      zoomedImageSrc,
      isSwiper,
      elPrev,
      elNext,
      isNoZoom,
      onTouchStart,
      zoomedImageElement,
      ...toRefs(data), //data
      imageSize,
      normalizedArticle,
      isInstall: computed(() => interfaceStore.state.supports.install),
      hasInteract: computed(() => interfaceStore.hasInteract.article),
      hasTouch: computed(() => interfaceStore.hasTouch),
      //svg
      BorderSvg,
      IconPlus,
      SvgArrow,
      CloseIcon
    };
  },
});
</script>

<template>
  <section ref="el"
           :key="normalizedArticle[0] + '_'"
           class="ArticleObject"
  >
    <div v-if="(type === 'article' && article) || (article)" class="doc">
      <component :is="isSwiper ? 'ui-swiper' : 'div'"
                 ref="Swiper"
                 :options="optionsSlider"
                 :data-swiper="isSwiper"
                 tag="ol"
                 class="wrapper"
                 :cursor="true"
                 :overflow="true"
                 :navigation="{
                   nextEl: elNext && elNext.$el,
                   prevEl: elPrev && elPrev.$el
                 }"
      >
        <component :is="isSwiper ? 'li' : 'div'"
                   v-for="(image, index) in normalizedArticle"
                   :key="`BuilderImageVideo-item-${index}`"
                   class="item slide"
                   :data-single="!isSwiper"
        >
          <div class="container-pic">
            <ui-picture :src="`/thematiques/${route.params.thematique}/contenu/${image}`"
                        class="media-swip -picture"
                        sizes="(min-width: 1024px) 1000px, 80vw"
                        cover="contain"
                        :force-visible="true"
            />
            <ui-button v-if="!(isNoZoom(index))"
                       ref="zoomEl"
                       theme="reset"
                       class="button-zoom swiper-no-swiping"
                       name="zoom"
                       data-variant="page.parent?.id"
                       aria-label="Bouton zoom"
                       :style="imageSize[index] ? {
                         right: `${imageSize[index]}px`
                       } : null"
                       @click="startZoom(`/thematiques/${route.params.thematique}/contenu/${image}`, $event)"
            >
              <span class="border" v-html="BorderSvg" />
              <span class="icon" v-html="IconPlus" />
            </ui-button>
          </div>
        </component>
      </component>
      <nav v-if="isSwiper"
           class="navigation"
           :data-variant="themeId"
      >
        <ui-button ref="elPrev"
                   class="btn -prev"
                   theme="reset"
                   title="Prev"
                   :icon="SvgArrow"
        />
        <ui-button ref="elNext"
                   class="btn -next"
                   theme="reset"
                   title="Next"
                   :icon="SvgArrow"
        />
      </nav>
      <!-- Zoomed Image Modal -->
      <div v-if="isZoomed"
           class="zoomed-image-container"
           @click="!hasTouch ? stopZoom() : null"
      >
        <img v-if="zoomedImageSrc"
             ref="zoomedImageElement"
             :src="zoomedImageSrc"
             class="zoomed-image"
        >
        <ui-button ref="zoomElClose"
                   theme="reset"
                   class="button-zoom-close"
                   name="no-zoom"
                   aria-label="Fermer le Zoom"
                   @click="stopZoom()"
        >
          <span class="icon" v-html="CloseIcon" />
        </ui-button>
        <div v-if="!hasInteract && isInstall"
             ref="zoomPicto"
             class="button-infos-touch"
        >
          <img src="/assets/interaction-touch.png">
        </div>
      </div>
    </div>
  </section>
</template>

